@import url(https://fonts.googleapis.com/css2?family=Aclonica&display=swap);
.card {
  background: #fff;
  height: 160px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 3px 2px 6px rgb(54, 54, 54);
  text-align: left;
  cursor: pointer;
  border: none;
}

.hvr-grow {
    display: inline-block;
    vertical-align: middle;
    transform: translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    transition-duration: 0.3s;
    transition-property: transform;
}

.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
    transform: scale(1.1);
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: black;
}

.card > .img-container > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.wrapper {
  background: #3f6b36;
  background-image: url("https://cdn.pixabay.com/photo/2018/10/12/21/09/meadow-3743023_1280.jpg");
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  overflow: auto;
  background-size: cover;
  background-attachment: fixed;
  padding-bottom: 20px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: rgb(217, 247, 133);
  font-family: 'Aclonica', sans-serif;
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.navbar {
    width: 100%;
    margin-bottom: 30px;
    background-color: rgb(18, 24, 18); 
}

.navbar-text {
    width: 100%;
    text-align: center;
    padding: 5px;
    margin: 0;
    color: white;
    word-spacing: .2em;
}
